import { initAos, initAosScale, initStaggeredAos } from './plugins/aos';
import { initLenis } from './plugins/lenis';
import { getLenis } from './plugins/lenis';
import { initTrMarquee } from './plugins/scroll-marquee';
import {
  initFooterOverlap,
  initSectionOverlapFull,
  initSectionOverlapNext,
  initSectionOverlapPrev,
} from './plugins/section-overlap';
import { showHideGrid } from './plugins/showHideGrid';
import { splitLines } from './plugins/split-text';

CustomEase.create('primary-ease', '0.62, 0.05, 0.01, 0.99');
CustomEase.create('primary-ease-out', '0.17,0.84,0.44,1');
CustomEase.create('smooth', '.15, .9, .34, .95');
CustomEase.create('custom', '.76, 0, .24, 1');

ScrollTrigger.defaults({
  markers: false,
});

function initLoad() {
  const navbar = document.querySelector('.navbar');
  const navLinks = navbar?.querySelectorAll('.nav-link');
  const brand = navbar?.querySelector('.nav-brand');
  const btn = navbar?.querySelector('.nav-btn');
  const toggle = navbar?.querySelector('.nav-toggle');

  const hero = document.querySelector('.hero');
  const head = hero?.querySelector('.h1');
  const subhead = hero?.querySelector('.text-16');
  const items = hero?.querySelectorAll('.hero-item');

  const splitHead = new SplitText(head, {
    type: 'lines',
    mask: 'lines',
    linesClass: 'line',
  });
  const splitSubhead = new SplitText(subhead, {
    type: 'lines',
    mask: 'lines',
    linesClass: 'line',
  });

  gsap.set([navLinks, brand, btn, toggle], { y: '2rem', autoAlpha: 0 });
  gsap.set(splitHead.lines, { y: '105%' });
  gsap.set(splitSubhead.lines, { y: '105%' });
  gsap.set(items, { scale: 0, autoAlpha: 0, transformOrigin: '0% 100%' });

  const tl = gsap.timeline();
  tl.to(brand, { y: '0rem', autoAlpha: 1, duration: 0.8, ease: 'power3.out' })
    .to(
      navLinks,
      {
        y: '0rem',
        autoAlpha: 1,
        duration: 0.8,
        ease: 'power3.out',
        stagger: 0.05,
      },
      '<0.2'
    )
    .to(
      [btn, toggle],
      {
        y: '0rem',
        autoAlpha: 1,
        duration: 0.6,
        ease: 'power3.out',
      },
      '<0.2'
    );
  tl.to(
    splitHead.lines,
    {
      y: '0%',
      duration: 1.2,
      ease: 'custom',
      stagger: 0.1,
    },
    '<0.2'
  )
    .to(
      splitSubhead.lines,
      {
        y: '0%',
        duration: 1.2,
        ease: 'custom',
        stagger: 0.1,
      },
      '<0.2'
    )
    .to(
      items,
      {
        scale: 1,
        autoAlpha: 1,
        duration: 1.2,
        ease: 'primary-ease',
        stagger: {
          ammount: 0.3,
          from: 'start',
        },
      },
      '<0.2'
    );
}

function testiCustomMouse() {
  const prevTrigger = document.querySelector('.swiper-nav.is-prev');
  const nextTrigger = document.querySelector('.swiper-nav.is-next');
  const prevMouse = document.querySelector('.testi-mouse.is-prev');
  const nextMouse = document.querySelector('.testi-mouse.is-next');

  if (!prevMouse || !nextMouse) return;

  let mouseX = 0;
  let mouseY = 0;

  let x = 0;
  let y = 0;

  let vx = 0; // velocity
  let vy = 0;

  const smoothing = 0.15; // global smoothness
  const friction = 0.8; // momentum decay
  const responsiveness = 0.2; // how fast it chases mouse

  const setPrevX = gsap.quickSetter(prevMouse, 'x', 'px');
  const setPrevY = gsap.quickSetter(prevMouse, 'y', 'px');

  const setNextX = gsap.quickSetter(nextMouse, 'x', 'px');
  const setNextY = gsap.quickSetter(nextMouse, 'y', 'px');

  window.addEventListener('mousemove', (e) => {
    mouseX = e.clientX - window.innerWidth / 2;
    mouseY = e.clientY - window.innerHeight / 2;
  });

  gsap.ticker.add(() => {
    // Hitung difference antara mouse dan cursor
    const dx = mouseX - x;
    const dy = mouseY - y;

    // Responsiveness: seberapa cepat ngikut mouse
    vx += dx * responsiveness;
    vy += dy * responsiveness;

    // Friction: kasih inertia (lebih halus)
    vx *= friction;
    vy *= friction;

    // Update posisi
    x += vx * smoothing;
    y += vy * smoothing;

    setPrevX(x);
    setPrevY(y);

    setNextX(x);
    setNextY(y);
  });

  const prevTl = gsap.timeline({ paused: true });
  prevTl.to(prevMouse, { autoAlpha: 1, scale: 1, duration: 0.3, ease: 'power3.out' });

  const nextTl = gsap.timeline({ paused: true });
  nextTl.to(nextMouse, { autoAlpha: 1, scale: 1, duration: 0.3, ease: 'power3.out' });

  prevTrigger?.addEventListener('mouseenter', () => {
    prevTl.play();
  });

  prevTrigger?.addEventListener('mouseleave', () => {
    prevTl.reverse();
  });
  nextTrigger?.addEventListener('mouseenter', () => {
    nextTl.play();
  });

  nextTrigger?.addEventListener('mouseleave', () => {
    nextTl.reverse();
  });

  // CLICK feedback micro interaction
  const prevClickTl = gsap
    .timeline({ paused: true })
    .to(prevMouse, {
      scale: 0.9,
      duration: 0.12,
      ease: 'power2.out',
    })
    .to(prevMouse, {
      scale: 1,
      duration: 0.12,
      ease: 'power2.in',
    });

  const nextClickTl = gsap
    .timeline({ paused: true })
    .to(nextMouse, {
      scale: 0.9,
      duration: 0.12,
      ease: 'power2.out',
    })
    .to(nextMouse, {
      scale: 1,
      duration: 0.12,
      ease: 'power2.in',
    });

  const clickPrev = () => {
    prevClickTl.restart();
  };

  const clickNext = () => {
    nextClickTl.restart();
  };

  prevTrigger?.addEventListener('click', clickPrev);
  nextTrigger?.addEventListener('click', clickNext);
}

function initServiceAccordion(
  containerSelector = '.service-content-list',
  allowMultipleOpen = false
) {
  const container = document.querySelector(containerSelector);
  if (!container) return;

  const items = container.querySelectorAll('.service-item');

  items.forEach((item, index) => {
    const text = item.querySelector('.service-item-content');
    const icon = item.querySelector('.plus-icon');
    if (!text || !icon) return;

    // Set ikon awal (kalau pakai rotate)
    gsap.set(icon, { rotate: 0, opacity: 0.5 });
    gsap.set(text, { height: 0, autoAlpha: 0 });

    const tl = gsap.timeline({ paused: true, reversed: true });

    tl.to(text, {
      height: 'auto',
      duration: 0.3,
      ease: 'none',
    })
      .to(
        text,
        {
          autoAlpha: 1,
          duration: 0.2,
          ease: 'power3.out',
        },
        '<'
      )
      .to(
        icon,
        {
          rotate: 90,
          opacity: 1,
          duration: 0.2,
          ease: 'power3.out',
        },
        '<'
      ); // ikon animasi dimulai bersamaan

    item._accordionTimeline = tl;

    // Event klik
    item.addEventListener('click', (e) => {
      if (e.target.closest('.service-item-content')) return;

      const isOpen = !tl.reversed();

      if (!allowMultipleOpen) {
        items.forEach((other) => {
          if (other !== item && !other._accordionTimeline.reversed()) {
            other.classList.remove('is-active');
            other._accordionTimeline.reverse();
          }
        });
      }

      if (isOpen) {
        item.classList.remove('is-active');
        tl.reverse();
      } else {
        item.classList.add('is-active');
        tl.play();
      }
    });

    // === Auto open item pertama ===
    if (index === 0) {
      item.classList.add('is-active');
      tl.play(0);
    }
  });
}

