function swiperTesti() {
  const swiper = document.querySelector('.swiper.is-testi');
  if (!swiper) return;
  const navPrev = swiper.querySelector('.swiper-nav.is-prev');
  const navNext = swiper.querySelector('.swiper-nav.is-next');

  const testiSwiper = new Swiper('.swiper.is-testi', {
    // Swiper options
    slidesPerView: 2.5,
    spaceBetween: 16,
    loop: false,
    speed: 800,
    breakpoints: {
      320: {
        slidesPerView: 1,
        spaceBetween: 16,
      },
      768: {
        slidesPerView: 'auto',
        spaceBetween: 16,
      },
      992: {
        slidesPerView: 'auto',
        spaceBetween: 16,
      },
    },
    navigation: {
      nextEl: navNext,
      prevEl: navPrev,
    },
  });
}

function scrollAnimations() {
  //hero scroll
  const hero = document.querySelector('.hero');
  if (!hero) return;
  const head = hero.querySelector('.hero-head');
  const content = hero.querySelector('.hero-content');

  gsap.to(head, {
    y: '10vh',
    scale: 0.75,
    autoAlpha: 0,
    duration: 1,
    scrollTrigger: {
      trigger: hero,
      start: 'top top',
      end: '40% top',
      scrub: true,
    },
  });
  gsap.to(content, {
    scale: 1.3,
    duration: 1,
    scrollTrigger: {
      trigger: hero,
      start: 'top top',
      end: 'bottom top',
      scrub: true,
    },
  });

  // reels scroll
  const reelsWrapper = document.querySelector('.section.is-reels');
  const reels = document.querySelector('.reels-wrapper');

  gsap.set(reels, { transformOrigin: 'top center' });

  gsap.from(reels, {
    scale: 0.6,
    duration: 1,
    scrollTrigger: {
      trigger: reelsWrapper,
      start: 'top bottom',
      end: 'bottom bottom',
      scrub: true,
    },
  });
}

function initHoverAnimations() {
  function initBtnHover() {
    const buttons = document.querySelectorAll('.btn');
    if (buttons.length === 0) return;

    buttons.forEach((button) => {
      const buttonText = button.querySelector('.btn-text');
      const buttonTextHover = button.querySelector('.btn-text.is-absolute');
      const buttonIcon = button.querySelector('.btn-icon');
      const buttonIconHover = button.querySelector('.btn-icon.is-absolute');

      if (!buttonText || !buttonTextHover) return; // kalau teks gak ada, skip button ini

      const tl = gsap.timeline({ paused: true });

      // === SplitText untuk teks ===
      const splitText = new SplitText(buttonText, { type: 'words' });
      const splitTextHover = new SplitText(buttonTextHover, { type: 'words' });

      tl.to(
        splitText.words,
        {
          yPercent: -100,
          duration: 0.8,
          ease: 'primary-ease',
        },
        0
      ).to(
        splitTextHover.words,
        {
          yPercent: -100,
          duration: 0.8,
          ease: 'primary-ease',
        },
        0
      );

      // === Animasi icon kalau ada ===
      if (buttonIcon && buttonIconHover) {
        tl.to(
          buttonIcon,
          {
            xPercent: 100,
            duration: 0.8,
            ease: 'primary-ease',
          },
          0
        ).to(
          buttonIconHover,
          {
            xPercent: 100,
            duration: 0.8,
            ease: 'primary-ease',
          },
          0
        );
      }

      // === Event hover ===
      button.addEventListener('mouseenter', () => tl.play());
      button.addEventListener('mouseleave', () => tl.reverse());
    });
  }

  function workHover() {
    const items = document.querySelectorAll('.work-item');
    if (!items.length) return;

    // Reset semua item
    const resetAll = () => {
      items.forEach((item) => {
        const img = item.querySelector('.work-img');
        gsap.to(item, {
          filter: 'grayscale(0%)',
          duration: 0.3,
          ease: 'power2.out',
        });
        gsap.to(img, {
          scale: 1,
          duration: 0.35,
          ease: 'power3.out',
        });
      });
    };

    items.forEach((item) => {
      const img = item.querySelector('.work-img');

      // Ketika mouse masuk ke item
      item.addEventListener('mouseenter', () => {
        items.forEach((other) => {
          const otherImg = other.querySelector('.work-img');

          if (other === item) {
            // item yang dihover
            gsap.to(other, {
              filter: 'grayscale(0%)',
              duration: 0.3,
            });
            gsap.to(otherImg, {
              scale: 1.05,
              duration: 0.35,
            });
          } else {
            // item lain
            gsap.to(other, {
              filter: 'grayscale(100%)',
              duration: 0.3,
            });
            gsap.to(otherImg, {
              scale: 1,
              duration: 0.35,
            });
          }
        });
      });

      // Ketika mouse keluar dari item → reset
      item.addEventListener('mouseleave', resetAll);
    });
  }

  initBtnHover();
  workHover();
}

function initNav() {
  const navbar = document.querySelector('.navbar');
  const navMobile = document.querySelector('.nav-mobile');
  if (!navbar || !navMobile) return;

  const toggle = navbar.querySelector('.nav-toggle');
  const toogleText = toggle?.querySelector('.btn-text');
  const toogleTextHover = toggle?.querySelector('.btn-text.is-absolute');
  const toogleIcon = toggle?.querySelector('.toggle-icon');

  const navLinks = navMobile.querySelectorAll('.nav-link');
  const btn = navMobile.querySelector('.nav-btn');

  const tl = gsap.timeline({ paused: true });

  gsap.set(navLinks, { x: '2rem', autoAlpha: 0 });
  gsap.set(btn, { x: '2rem', autoAlpha: 0 });

  tl.to(toogleText, { yPercent: -100, duration: 0.5, ease: 'power3.out' })
    .to(toogleTextHover, { yPercent: -100, duration: 0.5, ease: 'power3.out' }, '<')
    .to(toogleIcon, { rotate: 45, duration: 0.5, ease: 'power3.out' }, '<')
    .to(navMobile, { y: '100%', duration: 0.8, ease: 'custom' }, '<')
    .to(
      navLinks,
      {
        x: '0rem',
        autoAlpha: 1,
        duration: 0.6,
        ease: 'smooth',
        stagger: {
          amount: 0.2,
        },
      },
      '<0.5'
    )
    .to(
      btn,
      {
        x: '0rem',
        autoAlpha: 1,
        duration: 0.6,
        ease: 'smooth',
      },
      '<0.3'
    );

  let isOpen = false;

  // ===========================
  // TOGGLE NAV
  // ===========================
  toggle.addEventListener('click', () => {
    if (isOpen) {
      tl.reverse();
      lenis.start(); // <-- START ketika nav ditutup
      isOpen = false;
    } else {
      tl.play();
      lenis.stop(); // <-- STOP ketika nav dibuka
      isOpen = true;
    }
  });

  // ===========================
  // CLOSE ON RESIZE (DESKTOP)
  // ===========================
  window.addEventListener('resize', () => {
    if (window.innerWidth >= 992 && isOpen) {
      tl.reverse();
      lenis.start(); // <-- START lagi ketika close otomatis
      isOpen = false;
    }
  });

  // ===========================
  // CLOSE ON LINK CLICK
  // ===========================
  navLinks.forEach((link) => {
    link.addEventListener('click', () => {
      if (isOpen) {
        tl.reverse();
        lenis.start(); // <-- START setelah klik link
        isOpen = false;
      }
    });
  });
}

function initModal() {
  const modalTriggers = document.querySelectorAll('[data-modal-trigger]');
  const modalCloseButtons = document.querySelectorAll('[data-modal-close]');
  const modal = document.querySelector('.modal');

  const mainModal = modal?.querySelector('.form-modal');
  const mainForm = modal?.querySelector('.form-main');
  const formBtn = modal?.querySelector('.form-btn-wrap');

  let isOpen = false;

  gsap.set(modal, { autoAlpha: 0 });
  gsap.set(mainModal, { clipPath: 'inset(0% 0% 0% 100%)' });
  gsap.set(mainForm, { autoAlpha: 0 });
  gsap.set(formBtn, { autoAlpha: 0 });

  const openTl = gsap.timeline({ paused: true });
  openTl
    .to(modal, { autoAlpha: 1, duration: 0.3, ease: 'power2.out' })
    .to(mainModal, { clipPath: 'inset(0% 0% 0% 0%)', duration: 0.6, ease: 'custom' })
    .to(mainForm, { autoAlpha: 1, duration: 0.5, ease: 'power3.out' }, '<0.1')
    .to(formBtn, { autoAlpha: 1, duration: 0.5, ease: 'power3.out' }, '<0.1');
  const closeTl = gsap.timeline({ paused: true });
  closeTl
    .to(formBtn, { autoAlpha: 0, duration: 0.3, ease: 'power3.in' })
    .to(mainForm, { autoAlpha: 0, duration: 0.3, ease: 'power3.in' }, '<0.1')
    .to(mainModal, { clipPath: 'inset(0% 0% 0% 100%)', duration: 0.6, ease: 'custom' }, 0)
    .to(modal, { autoAlpha: 0, duration: 0.3, ease: 'power2.in' });

  modalTriggers.forEach((trigger) => {
    trigger.addEventListener('click', () => {
      if (!isOpen) {
        openTl.play(0);
        isOpen = true;
        lenis.stop(); // STOP ketika modal dibuka
      }
    });
  });

  modalCloseButtons.forEach((button) => {
    button.addEventListener('click', () => {
      if (isOpen) {
        closeTl.play(0);
        isOpen = false;
        lenis.start(); // START ketika modal ditutup
      }
    });
  });
}

function formControls() {
  const radios = document.querySelectorAll('.w-radio');

  radios.forEach((radio) => {
    const input = radio.querySelector('input[type="radio"]');

    if (!input) {
      console.warn('Radio input missing');
      return;
    }

    // Event saat label diklik
    radio.addEventListener('click', () => {
      // Reset semua di group yang sama
      const group = input.name;
      const all = document.querySelectorAll(`input[name="${group}"]`);

      all.forEach((r) => {
        r.closest('.w-radio').classList.remove('is-active');
      });

      // Aktifkan radio ini
      radio.classList.add('is-active');
    });
  });
}

function initRadioSync() {
  // sesuaikan selector jika wrapper/radio Webflow-mu beda
  const radioWrappers = document.querySelectorAll('.w-radio');

  radioWrappers.forEach((wrapper) => {
    const input = wrapper.querySelector('input[type="radio"]');
    if (!input) return;

    // Inisialisasi: jika input sudah checked di HTML, tambahkan class ke wrapper
    if (input.checked) {
      wrapper.classList.add('is-active');
    } else {
      wrapper.classList.remove('is-active');
    }

    // Event listener untuk sinkronisasi ketika value berubah
    // Gunakan 'change' karena lebih andal untuk radio groups
    input.addEventListener('change', () => {
      // reset semua wrapper dalam group yang sama
      const groupName = input.name;
      const groupInputs = document.querySelectorAll(`input[type="radio"][name="${groupName}"]`);

      groupInputs.forEach((i) => {
        const w = i.closest('.w-radio');
        if (!w) return;
        if (i.checked) w.classList.add('is-active');
        else w.classList.remove('is-active');
      });
    });

    // Jika kamu juga butuh klik pada label/ wrapper (opsional)
    wrapper.addEventListener('click', () => {
      // terkadang klik terjadi sebelum change; small timeout memastikan change diproses
      setTimeout(() => {
        const groupName = input.name;
        const groupInputs = document.querySelectorAll(`input[type="radio"][name="${groupName}"]`);
        groupInputs.forEach((i) => {
          const w = i.closest('.w-radio');
          if (!w) return;
          if (i.checked) w.classList.add('is-active');
          else w.classList.remove('is-active');
        });
      }, 10);
    });
  });
}

function initApp() {
  const mm = gsap.matchMedia();
  initAos();
  initAosScale();
  initStaggeredAos();
  initLenis();
  window.lenis = getLenis();
  showHideGrid();
  splitLines();
  initTrMarquee();

  mm.add('(min-width: 992px)', () => {
    initSectionOverlapFull();
    initSectionOverlapNext();
    initSectionOverlapPrev();
  });

  swiperTesti();
  testiCustomMouse();
  initServiceAccordion();
  initFaqAccordion();
  scrollAnimations();
  initModal();

  initLoad();
  initHoverAnimations();
  initNav();
  // formControls();
  initRadioSync();
}

document.addEventListener('DOMContentLoaded', () => {
  initApp();
});
