export function showHideGrid() {
  /* ==============================================
Show/Hide Grid on Keypress
============================================== */
  document.addEventListener('keydown', (event) => {
    if (event.shiftKey && event.key.toLowerCase() === 'g') {
      const gridOverlay = document.querySelector('.grid-guide') as HTMLElement | null;
      if (!gridOverlay) return;

      const isHidden = window.getComputedStyle(gridOverlay).display === 'none';
      gridOverlay.style.display = isHidden ? 'block' : 'none';
    }
  });
}
